.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
  height: 2em;
  justify-content: center;
}

.nav-button {
  height: 100%;
  width: fit-content;

  border: 0;

  background-color: lightcoral;

}




.portrait-exhibit {
  height: 80vh;
  width: 60vw;

  display: flex;
  justify-content: center;
}

.portrait-container {
  order: 1; 
  height: auto;
  width: 50%;
}


.portrait-description {
  order: 2;
  height: auto;
  width: 50%;
}